        <loading :active.sync="isLoading" 
        :is-full-page="true"></loading>
				<div v-if='loadingFailMessage'>Loading failed ({{loadingFailMessage}}). Please <button onclick='location.reload();'>refresh</button></div>
<div v-if='!content'>
Not found<br/>
</div>
<div class='menu-bar'>
<router-link class='item' :to="{ name: 'edit', params: { page: $route.params.name }}"><span v-if='!content'>Create</span><span v-else>Edit</span> {{$route.params.name}}</router-link>  <a target="_blank" class='item' :href='"https://whatsonchain.com/tx/"+wiki.tx.tx.h'>View Tx</a>
