<script>
    import Loading from 'vue-loading-overlay';
import wikiLoader from './wiki-loader'

const marked = require('marked');
const renderer = new marked.Renderer();
var oldLink = renderer.link;
renderer.link = function(href, title, text) {
var args = Array.prototype.slice.call(arguments);
var wikiPrefix = 'wiki:';
if (href.substring(0,wikiPrefix.length)==wikiPrefix) {
	var wikiName = href.substring(wikiPrefix.length);
	var titleStr = title?` title="${title}"`:'';
	return `<a href="#/wiki/${wikiName}" ${titleStr}>${text}</a>`;
}
  return oldLink.apply(this, [href,title,text]);
}
marked.setOptions({
breaks:true,
link:function(cap){
  return {href:'wiki:'+cap[1],title:null};
},
renderer
});

/*var MarkdownIt = require('markdown-it');
var md = new MarkdownIt({
linkify: true,
breaks:true
});
*/
export default {
components:{
	Loading
},
watch: {
    '$route' (to, from) {
console.log(to, from);
this.display(to.params);
      // react to route changes...
    }
  },  data() {
    return {
			wiki:{tx:{tx:{}}},
      content:'',
loadingFailMessage:'',
isLoading: false,
loaded: false
    }
  },
  methods: {
	 async display(params) {
		this.loadingFailMessage = '';
		this.isLoading = true;
		try {
			return await this.display1(params);
		} catch (e) {
			this.loadingFailMessage = ''+e;
			throw e;
		} finally {
			this.isLoading = false;
		}
	},
   async display1(params) {
//if (Math.random()<0.5) throw 'test '+Date();
var wiki = await wikiLoader.load(params);
var content=wiki?wiki.content:``;
this.loaded = true;
console.log(wiki);
this.wiki = wiki;
this.content = marked(content);
  }
  },
    async mounted() {
this.display(this.$route.params);
    }
}
</script>
