<template>
  <div>
  Page
  Name:<br/>
  <input type='text' size='50' v-model='name' :disabled='loaded'/><br/>
  Markdown Content:<br/>
  <textarea rows='30' cols='80' v-model='content'/><br/>

      <MoneyButton
      label="Create/Update"
			:devMode="false"
			successMessage="update done."
			:outputs="outputs"
         />

</div>
</template>
<script>
import MoneyButton from 'vue-money-button'
import wikiLoader from './wiki-loader'
import bsv from 'bsv'
export default {
  components: {
    MoneyButton
  },
  data() {
    return {
			loaded: false,
      name: '',
      content: ''
    };
  },
watch: {
    '$route' (to, from) {
this.load(to.params);
    }
  },
computed: {
outputs() {
	var opreturn = this.opReturn;
	return [{
      script: opreturn,
      amount: '0',
      currency: 'BSV'
    }]
},
opReturn() {
	var out = bsv.Script.buildSafeDataOut(['19HxigV4QyBv3tHpQVcUEQyq1pzZVdoAut',this.content,'text/markdown','utf8',this.name+'.md']);

	console.log(out.toASM());
return out.toASM();
}
},
	methods:{
		async load(params) {
		this.loaded = false;
		this.name = this.content = ''
		let loader = this.$loading.show();
    try {
    var wiki = await wikiLoader.load({name:params.page});
			if (wiki) {
				this.loaded = true;
				this.name = params.page;
				this.content = wiki.content;
			}
    }finally{
			loader.hide();
		}
		}
	},
  async mounted() {
		await this.load(this.$route.params);
  }
}
</script>
